import banner from '../components/banner.vue'
import topBar from '../components/topBar.vue'
export default {
  components: {
    banner: banner,
    topBar: topBar
  },
  data() {
    return {
      swiperList: '', // 轮播图
      tabList: [{
        name: '发现',
        category_id: 0
      }, {
        name: '最新',
        category_id: 1
      }, {
        name: '关注',
        category_id: 2
      }],                // 菜单
      category_id: 0,    // 菜单选中
      articleList: [],   // 文章列表
      page: {
        total: 1,
        current_page: 1,
        last_page: 1,
        per_page: 15
      },
      mythemes: ''
    }
  },
  onLoad() {
    let that= this
    that.getBanner()
    that.getFindList()
  },
  onShow() {
    this.getMyTheme()
    
    let that= this,
      id= this.category_id
    that.page.current_page= 1
    that.articleList= []
    switch (id) {
      case 0:
        that.getFindList()
        break
      case 1:
        that.getNewList()
        break
      case 2:
        if(that.$store.state.token) {
          that.getFocusList()
        } else {
          this.category_id= 0
        }
        break
    }  
  },
  watch: {
    category_id (val) {
      let that= this
      that.page.current_page= 1
      that.articleList= []
      switch (val) {
        case 0:
          that.getFindList()
          break
        case 1:
          that.getNewList()
          break
        case 2:
          that.getFocusList()
          break
      }
    }
  },
  methods: {
    // 上拉加载更多
    lower() {
      let that= this,
        val= this.category_id
      if(that.page.current_page < that.page.last_page) {
        that.page.current_page += 1
        switch (val) {
          case 0:
            that.getFindList()
            break
          case 1:
            that.getNewList()
            break
          case 2:
            that.getFocusList()
            break
          default:
            break
        }  
      } else {
        uni.showToast({
        	title: '亲！没有更多数据了哦！',
          icon: 'none'
        })
      }
      
    },
    // 设置page信息
    setPage(obj) {
      let that= this
      for(let item in obj) {
        if(item !== 'data') {
          that.page.item= obj[item]
        }
      }
    },
    
    // 首页轮播图图片
    getBanner() {
      let that= this
      let params = {
        url: that.$api.homebanners
      }
      that.$httpRequest(params).then(res => {
        if(res.code === 1) {
          // 替换轮播图图片路径数据
          that.swiperList = res.data.list
        } else {
          uni.showToast({
          	title: '轮播图图片加载失败',
            icon: 'none'
          })
        }
      }, error => {
        console.log(error);
      })
    },
    
    // 发现文章列表
    getFindList() {
      let that= this
      let params = {
        url: that.$api.recomList,
        data: {
          page: that.page.current_page
        }
      }
      that.$httpRequest(params).then(res => {
        if(res.code === 1) {
          if(that.articleList.length < 1) {
            that.articleList= res.data.list.data
          } else if(that.page.current_page < res.data.list.last_page) {
            that.articleList= that.articleList.concat(res.data.list.data)
          }
          // 设置页面信息
          that.setPage(res.data.list)
        } else  {
          uni.showToast({
          	title: '发现文章列表加载失败',
            icon: 'none'
          })
        }
      })
    },
    
    // 最新文章列表
    getNewList() {
      let that= this
      let params = {
        url: that.$api.newList,
        data: {
          page: that.page.current_page
        }
      }
      that.$httpRequest(params).then(res => {
        if(res.code === 1) {
          if(that.page.current_page >= res.data.list.last_page && that.articleList.length < 1) {
            that.articleList= res.data.list.data
          } else {
            that.articleList= that.articleList.concat(res.data.list.data)
          }
          // 设置页面信息
          that.setPage(res.data.list)
        } else {
          uni.showToast({
          	title: '最新文章列表加载失败',
            icon: 'none'
          })
        }
      })
    },
    
    // 关注文章列表
    getFocusList() {
      let that= this
      let params = {
        url: that.$api.focusList,
        data: {
          page: that.page.current_page
        }
      }
      that.$httpRequest(params).then(res => {
        if(res.code === 1 ) {
          if(that.page.current_page >= res.data.list.last_page && that.articleList.length < 1) {
            that.articleList= res.data.list.data
          } else if(res.data.list){
            that.articleList= that.articleList.concat(res.data.list)
          }
          // 设置页面信息
          that.setPage(res.data.list)
        } else {
          uni.showToast({
          	title: '关注文章列表加载失败',
            icon: 'none'
          })
        }
      })
    },
    // 获取我关注的话题列表
    getMyTheme() {
      let that= this,
        params= {
          url: that.$api.myThemes
        }
      that.$httpRequest(params).then(res => {
        that.mythemes= res.data.mythemes
      })
    },
    
    // 去发表文章
    goRelease() {
      uni.navigateTo({
        url: './release/release'
      })
    },
    
    // 选择分类
    selectTab(item, index) {
      this.category_id = item.category_id
    },
    
    // 文章详情页
    goInfo(item) {
      uni.navigateTo({
        url: './article/detail?article_id=' + item
      })
    }
  }
}
